import {FC, Fragment, useEffect, useState} from "react";
import ModalMade from "../../../layOutComponent/Modal/Modal";
import style from "./../contactStyle.module.scss"
import InputMade from "../../../layOutComponent/Input/InputMade";
import {validateInputEmail, ValidateRequiredInput} from "../../../common/ValidaeModalContact/ValidateContactModal";
import {Button} from "@material-ui/core";
import RateReviewIcon from '@material-ui/icons/RateReview';
import MessageArea from "./MessageArea";
import SendIcon from '@material-ui/icons/Send';
import {putData, stateProps as contactsStateProps} from "../../../Store/ContactsReducer";
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../../Store/Store";
import Loader from "../../../common/Loader/Loader";
import SuccessModal from "../../../layOutComponent/successModal/SuccesedModal";


interface Props {
    title: string,

}

const SendFormComponent: FC<Props> = ({
                                          title,


                                      }) => {

    const dataContacts = useSelector<AppRootStateType, contactsStateProps> (state => state.Contacts)
    const dispatch = useDispatch()



    const [isOpen, setIsOpen] = useState<boolean>(false)

    //values of Email, Password, Confirm Password
    const [name, setName] = useState<string>(dataContacts.nameValue)
    const [email, setEmail] = useState<string>(dataContacts.emailValue)
    const [message, setMessage] = useState<string>(dataContacts.emailValue)
    const [isDisabledSend, setIsDisabledSend] = useState <boolean> (dataContacts.isBtnDisabled)

    //isValid
    const [isValidName, setIsValidName] = useState<boolean>(false)
    const [isValidEmail, setIsValidEmail] = useState<boolean>(false)



    //Error
    const [errorName, setErrorName] = useState<string>('')
    const [errorEmail, setErrorEmail] = useState<string>('')


    //modal for sendMessage
    const [isOpnMessageArea, setIsOpnMessageArea] = useState<boolean>(false)

    useEffect(()=>{
        if (name && email && message){
            if (errorName === ''&& errorEmail===''){
                setIsDisabledSend(false)
            }
        }
    },)

    const onShowModal = () => {
        setIsOpen(!isOpen)
    }
    const onShowModalForMessage = () => {
        setIsOpnMessageArea(!isOpnMessageArea)
    }


    const onChangeNameHandler = (nameValue: string) => {
        ValidateRequiredInput(nameValue, setName, setErrorName, setIsValidName)
    }

    const onChangeEmailHandler = (emailValue: string) => {
        validateInputEmail(emailValue, setEmail, setErrorEmail, setIsValidEmail)
    }


    const onSendMessageDataHandler = () => {
        if(name && email && message) {
            dispatch (putData(name, email, message))
        }
        setName('')
        setEmail('')
        setMessage('')
        setIsDisabledSend(true)


    }



    return (
        <div className={style.mainButton}>
            <h3 role="button" onClick={onShowModal}>
                Send Message From that Page
            </h3>

            <ModalMade modal={isOpen}
                       setModal={setIsOpen}
                       description={true}
            >
                <span>
                    <InputMade placeholder={'E-mail'}
                               value={email}
                               onChangeText={onChangeEmailHandler}
                               error={isValidEmail}
                               errorMes={errorEmail}
                               setError={setIsValidEmail}
                    />
                </span>
                <span>
                     <InputMade placeholder={'Name'}
                                value={name}
                                onChangeText={onChangeNameHandler}
                                error={isValidName}
                                setError={setIsValidName}
                                errorMes={errorName}/>
                </span>
                <div>
                    <span>
                        <Button color="primary" onClick={onShowModalForMessage}>
                            <RateReviewIcon color="primary"/> <p>WRITE YOU MESSAGE</p>
                        </Button>

                    </span>

                    <MessageArea isOpen={isOpnMessageArea}
                                 handleClose={onShowModalForMessage}
                                 messageValue = {message}
                                 setMessageValue = {setMessage}
                    />
                </div>
                <div>
                    <Button disabled = {isDisabledSend}
                    onClick = {onSendMessageDataHandler}><SendIcon/> <h2>SEND</h2></Button>
                </div>

                <Fragment>
                    {
                        dataContacts.status === 'loading'
                            ? <Loader/>
                            : ''
                    }
                </Fragment>
                {
                    dataContacts.isSuccessModal && <SuccessModal open={dataContacts.isSuccessModal}/>
                }
            </ModalMade>



        </div>
    )
}

export default SendFormComponent