import React, {ChangeEvent, FC} from "react";
import {Button, Modal, TextField} from "@material-ui/core";
import DoneOutlineIcon from '@material-ui/icons/DoneOutline';
import style from "./../contactStyle.module.scss"

interface Props {
    isOpen: boolean;
    handleClose: (isOpen: boolean) => void
    messageValue: string
    setMessageValue: (message: string) => void
}

export const MessageArea: FC<Props> = ({
                                           isOpen,
                                           handleClose,
                                           messageValue,
                                           setMessageValue

                                       }) => {



    const onChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        setMessageValue(event.currentTarget.value)
    }

    return (<div>
        <Modal
            open={isOpen}
            onClose={handleClose}
            className={style.areaMessage}
        >
            <div>
                <div>
                    <TextField
                        onChange ={onChangeHandler}
                        value = {messageValue}
                        margin="normal"
                        multiline
                        rows={5}
                        placeholder={'This is where you leave your messages. This field is required to fill out' +
                        ' and send a message. After filling, click "OK" to continue'}/>
                </div>
                <div>
                    <Button color="primary"
                    onClick = {()=> handleClose(!isOpen)}
                    ><DoneOutlineIcon/><p>OK</p></Button>
                </div>


            </div>

        </Modal>
    </div>)
}

export default MessageArea