export const validateInputEmail = (value:string,
                                   setEmail:(value: string) => void,
                                   setErrorMes:(title: string)=> void,
                                   setValid:(isValid: boolean)=> void) => {

    const regularEmail = /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;

    setEmail(value)
    if (value.trim() === '') {
        setErrorMes('Required')
        setValid(true)
    } else if (!regularEmail.test(value)) {
        setValid(true)
        setErrorMes('Email entered incorrectly')

    } else {
        setErrorMes('')
        setValid(false)
    }
}

export const ValidateRequiredInput = (value: string,
                                      setValue: (value: string) => void,
                                      setErrorMess: (title: string) => void,
                                      setValid: (isValid: boolean) => void) => {
    setValue(value)
    if (value.trim() === '') {
        setErrorMess('Required')
        setValid(true)
    } else {
        setErrorMess('')
        setValid(false)
    }
}