import {FC, Fragment, useState} from "react";
import {Snackbar} from "@material-ui/core";
import {Alert} from "@material-ui/lab";


interface Props {
    open: boolean,

}

const SuccessModal : FC <Props> = ({
                                       open,


                                   }) => {
   const [isOpen, setIsOpen] = useState <boolean> (open)
    const onHandleClose = () => {
        setIsOpen(!isOpen)
    }
    return <Fragment>
        <Snackbar open={isOpen} autoHideDuration={6000} onClose={onHandleClose}>
            <Alert severity="success">You message was sent</Alert>
        </Snackbar>
    </Fragment>
}

export default SuccessModal