import React, {FC} from 'react';
import style from './Modal.module.scss'


interface ModalType {
    modal: boolean;
    setModal: (value: boolean) => void;
    description: boolean



}


const ModalMade: FC<ModalType> = ({modal, setModal, description,
                                  children,

                                  }) => {
    const finalModalClassName = `${style.modal} ${modal ? style.active : ""}`;
    const finalModalContentClassName = `${style.modal_content} ${modal ? style.active : ""}`;



    return (
        <div className={finalModalClassName} onClick={() => setModal(false)}>
            <div className={finalModalContentClassName} onClick={e => e.stopPropagation()}>
                {children}
                { description &&
                    <div className={style.description}>
                    <span>
                        <h1>*</h1>
                    </span>
                        <span>
                        <p> - All input fields are required! In order to send a message from this page, you need
                            to fill in all the fields!</p>
                    </span>

                    </div>
                }


            </div>
        </div>
    );
};

export default ModalMade;